          {
            <span class="hljs-attr">value</span>: <span class="hljs-string">'https://www.supermapol.com/realspace/services/map--world/rest/maps/World_Google'</span>,
            <span class="hljs-attr">label</span>: <span class="hljs-string">'google'</span>
          }
        ],
        <span class="hljs-attr">url</span>: <span class="hljs-string">'https://www.supermapol.com/realspace/services/3D-dixingyingxiang/rest/realspace/datas/MosaicResult'</span>,
        <span class="hljs-attr">alpha</span>: <span class="hljs-number">1</span>,
        <span class="hljs-attr">brightness</span>: <span class="hljs-number">1</span>,
        <span class="hljs-attr">contrast</span>: <span class="hljs-number">1</span>
      }
    },
    <span class="hljs-attr">methods</span>: {
      <span class="hljs-title function_">ready</span>(<span class="hljs-params">cesiumInstance</span>) {
        <span class="hljs-keyword">const</span> { <span class="hljs-title class_">Cesium</span>, viewer } = cesiumInstance
        <span class="hljs-variable language_">this</span>.<span class="hljs-property">cesiumInstance</span> = cesiumInstance
      },
