<h2>属性</h2>
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>url</td>
<td>String</td>
<td></td>
<td><code v-pre="">required</code> 超图 iserver 影像服务地址。</td>
</tr>
<tr>
<td>name</td>
<td>String</td>
<td></td>
<td><code v-pre="">optional</code> 影像图层名称。</td>
</tr>
<tr>
<td>minimumLevel</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> 最小层级。</td>
</tr>
<tr>
<td>maximumLevel</td>
<td>Number</td>
<td><code v-pre="">20</code></td>
<td><code v-pre="">optional</code> 最大层级。</td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>参考超图官方文档: <strong><a href="http://support.supermap.com.cn:8090/webgl/Build/Documentation/SuperMapImageryProvider.html">SuperMapImageryProvider</a></strong></li>
</ul>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例。</td>
</tr>
<tr>
<td>errorEvent</td>
<td>TileProviderError</td>
<td>当图层的提供者发生异步错误时触发, 返回一个 TileProviderError 实例。</td>
</tr>
<tr>
<td>readyPromise</td>
<td>ImageryProvider</td>
<td>当图层可用时触发, 返回 ImageryProvider 实例。</td>
</tr>
</tbody>
</table>
<hr>
</body></html></section></template>

<script>
    export default {
      data() {
        return {
          options: [
            {
              value: 'https://www.supermapol.com/realspace/services/3D-dixingyingxiang/rest/realspace/datas/MosaicResult',
              label: '四川地图'
            },
            {
              value: 'https://www.supermapol.com/realspace/services/map--world/rest/maps/World_Google',
              label: '谷歌地图'
            }
          ],
          url: 'https://www.supermapol.com/realspace/services/3D-dixingyingxiang/rest/realspace/datas/MosaicResult',
          alpha: 1,
          brightness: 1,
          contrast: 1
        }
      },
      methods: {
        ready(cesiumInstance) {
          const { Cesium, viewer } = cesiumInstance
          this.cesiumInstance = cesiumInstance
        },
        readyPromise() {
          const { Cesium, viewer } = this.cesiumInstance
          viewer.zoomTo(this.$refs.imageryProvider.providerContainer.imageryLayer)
        }
      }
    }
  </script>